body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;background-color:#121212}a{text-decoration:none;color:#03dac6}.container{display:grid;grid-template-columns:[empty] 1fr [content] 2fr [sidebar] 1fr}@media only screen and (min-device-width: 320px) and (max-device-width: 480px) and (-webkit-min-device-pixel-ratio: 2){.container{grid-template-columns:[content] 1fr;font-size:.8em}}.content{grid-column:content}blockquote{border-left:2px solid gray;margin:4px 0 4px 8px;padding-left:8px}
